.main {
  display: flex;
  flex-wrap: wrap;
  padding: 50px 0 73px;

  &__row {
    display: flex;
    flex-grow: 1;
    justify-content: space-between;
    align-items: center;
    max-width: 944px;
    padding-bottom: 46px;
    margin: 0 auto;

    &:last-child {
      padding-bottom: 0;
    }

  }
}
